// import smplr package
import {Soundfont, SplendidGrandPiano, DrumMachine, getDrumMachineNames} from "smplr";

// create Audio context
const context = new AudioContext();

//creating instruments
const marimba = new Soundfont(context, {instrument: "marimba"});
const piano =  new SplendidGrandPiano(context);
const flute = new Soundfont(context, {instrument: "flute"});
const cello= new Soundfont(context, {instrument: "cello"});

const allDrums = getDrumMachineNames();
const drums = new DrumMachine(context, {instrument: "Casio-RZ1"})

//play notes attached to HTML buttons
document.getElementById("marimba").onclick = ()=>{
    context.resume()
    marimba.start({note: 60, velocity: 80});
    setTimeout(()=>{marimba.stop(60)}, 1000)
}

document.getElementById("piano").onclick = ()=>{
    context.resume()
    piano.start({note: 60, velocity: 80});
    setTimeout(()=>{piano.stop(60)}, 1000)
}

document.getElementById("flute").onclick = ()=>{
    context.resume()
    flute.start({note: 72, velocity: 80});
    setTimeout(()=>{flute.stop(72)}, 1000)
}

document.getElementById("cello").onclick = ()=>{
    context.resume()
    cello.start({note: 48, velocity: 80});
    setTimeout(()=>{cello.stop(48)}, 1000)
}

document.getElementById("drums").onclick = ()=>{
    context.resume()
    drums.start({note: "kick"});
    drums.start({note: "ride"})
    setTimeout(()=>{drums.start({note: "snare"}); drums.start({note: "ride"})}, 500)
    setTimeout(()=>{drums.start({note: "kick"}); drums.start({note: "ride"})}, 1000)
    setTimeout(()=>{drums.start({note: "snare"}); drums.start({note: "ride"})}, 1500)
    setTimeout(()=>{drums.start({note: "ride"}); drums.start({note: "ride"})}, 2000)
}





